import React, { useEffect, useState, useRef } from "react";



import { getStore, gen16CodeId, ObjectKeys,  parseDate, updStore, _monthNames, copyToClipboard, parseDateMMDDYYYY, fetchPostH, getUrlServerSide } from "../../../stores/Utils";



import "../component/dashboard.css"
import { OpenModal } from "../../../components/DialogHRM";
import AddEducation from "./EducationAdd";
import { SvgIcon_Cmpt } from "../../../components";
import UpdEducation from "./EducationUpdate";
import DeleteAlert from "../component/deleteAlert";



const frmNm = "upd_client"



const Svg = SvgIcon_Cmpt();




const EducationDashboard  = ({ openUpd,   close }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [view, set_view] = useState(1);


	const updObs =  ()=>{
		set_obs(gen16CodeId())
	}
	

	const addEducation = async (v)=>{
		let ffm = {...getStore("upd_client")};

		let schoolHistory = ffm?.schoolHistory || [];
		schoolHistory.push(v)
		

		let bdyq2 = {
			query: "updateClientNotary",
			params:{
				"clientNotaryId": ffm["clientNotaryId"]
			},
			data2update: {schoolHistory }
		};

		// console.log(bdyq2)
		
		/*  */
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			ffm.schoolHistory = schoolHistory;
			updStore("upd_client", ffm);
			updObs()
		}
		
	}


	
	const updEducation = async (v, indX2)=>{
		let ffm = {...getStore("upd_client")};

		let schoolHistory = ffm?.schoolHistory || [];
		schoolHistory[indX2] = v;


		let bdyq2 = {
			query: "updateClientNotary",
			params:{
				"clientNotaryId": ffm["clientNotaryId"]
			},
			data2update: {schoolHistory }
		};
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			ffm.schoolHistory = schoolHistory;
			updStore("upd_client", ffm);
			updObs()
		}
		
	}
	
	const rmvEducation = async ( indX2)=>{
		let ffm = {...getStore("upd_client")};

		let schoolHistory = ffm?.schoolHistory || [];
		schoolHistory.splice(indX2,1);

		let bdyq2 = {
			query: "updateClientNotary",
			params:{
				"clientNotaryId": ffm["clientNotaryId"]
			},
			data2update: {schoolHistory }
		};
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			ffm.schoolHistory = schoolHistory;
			updStore("upd_client", ffm);
			updObs()
		}
		
	}



	const openUpdView =  () => {

		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<AddEducation confirm={addEducation} />
		OpenModal(modal_data);
	}

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			// let user = {...getStore("upd_client")};
			set_obs(gen16CodeId())
		}
	});


	
	
	

	let list = getStore("upd_client")?.schoolHistory || [];

	return (
		<div className=" ">
		
			<div className={"detailClient"}>
				<div className="_dsplFlx ">
					<p className="form_title">
						Educacion
					</p>
					<div className="flexSpace"/>
					<p className="group_title addBtn" onClick={openUpdView}>
						Agregar Escuela
					</p>
				</div>
			</div>
				
			<div className=" ">
				{list.map((item2, indX2)=> 
					<EducationItem  
						residence={item2} 
						key={indX2}
						index={indX2}
						updEducation={updEducation}
						deleteSchool={rmvEducation}
					/>
				)}
			</div>
		</div>
	);
};

export default EducationDashboard;







const EducationItem  = ({ residence,   updEducation, index, deleteSchool }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const updObs  =  (v)  => {
		set_obs( gen16CodeId());
	};

	const copyToClpb  =  (v)  => {
		copyToClipboard( v,updObs);
	};

	const _updEducation  =  (v)  => {
		updEducation(v,index)
	};

	const openUpdView =  () => {

		updStore("upd_education", residence);
		updStore("upd_education_bck", JSON.parse(JSON.stringify(residence)));
		

		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<UpdEducation confirm={_updEducation} />
		OpenModal(modal_data);
	}

	const confirmDelete =  (v)  => {
		deleteSchool(index);
	};



	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <DeleteAlert 
				confirm={confirmDelete}
				lbl_p={"Eliminar Escuela"} 
				lbl_span={"Una vez eliminado no podra ser recuperado"}
			/>
			OpenModal(modaldata);
		}
	}

	// confirm, 

	return (

		<div className="_dsplFlx ">
			<div className="flexSpace"/>
			<div className={`card_address modern `}  >
				<div className="_dsplFlx ">
					<div className="title_card" onClick={()=>copyToClpb(residence.schoolType)}>
						{residence.schoolType}
					</div>
					<div className="title_card" onClick={()=>copyToClpb(residence.schoolName)}>
						{residence.schoolName}
					</div>
					<div className="flexSpace"/>
					<div className={"icon_action _dsplFlx"}>
						<div className={"menu_icon _dsplFlx"}> 
							<div className="flexSpace"/>
							<Svg 
							name={"more_vert"}	/>
						</div>
						<div className={"menu_icon_acc _dsplFlx"}>
							<div 	onClick={openUpdView}>
								<Svg name={"outline_edit"}	/>
							</div>
							<div className="flexSpace"/>
							<div 	onClick={OpenDeleteAlert}>
								<Svg name={"outline_delete"}	/>
							</div>
						</div>
					</div>
				</div>
				<div className="body_card">
					<div className=" city_state">
						<div className="h4_gsans"  onClick={()=>copyToClpb(residence.addressLineOne)}>
							{residence.addressLineOne}
						</div>
					</div>
					<div className="_dsplFlx city_state">
						<div className="h4_gsans"  onClick={()=>copyToClpb(residence.city)}>
							{residence.city}
						</div>
						<div className="h4_gsans" onClick={()=>copyToClpb(residence.state)}>
							{residence.state}
						</div>
						<div className="h4_gsans" onClick={()=>copyToClpb(residence.zipcode)}>
							{residence.zipcode}
						</div>
					</div>
				</div>
				<div className="">
					<div className="_dsplFlx city_state">
						<div className="h5_gsans"  onClick={()=>copyToClpb(residence.country)}>
							{residence.country}
						</div>
						<div className="flexSpace"/>	
				
						<div className="h5_gsans"  onClick={()=>copyToClpb(residence?.fromDate?.month)}>
							{residence?.fromDate?.month}
						</div>
						<div className="h5_gsans" >
							{"/"}
						</div>
						<div className="h5_gsans"  onClick={()=>copyToClpb(residence?.fromDate?.year)}>
							{residence?.fromDate?.year}
						</div>
						<div className="h5_gsans" >
							{" - "}
						</div>
						<div className="h5_gsans"  onClick={()=>copyToClpb(residence?.toDate?.month)}>
							{residence?.toDate?.month}
						</div>
						<div className="h5_gsans" >
							{"/"}
						</div>
						<div className="h5_gsans"  onClick={()=>copyToClpb(residence?.toDate?.year)}>
							{residence?.toDate?.year}
						</div>
					</div>
				</div>
			</div>
			<div className="flexSpace"/>
		</div>
	)
}











const compareJSON = (fls,obj, bkUp) =>{
	let r = 0;
	let data = {}
	for(let i in fls){
		let k1 = fls[i];
		if(obj[k1] ){
			if(typeof obj[k1] === "object"){
				ObjectKeys(obj[k1]).map(fk2=>{
					if(obj?.[k1]?.[fk2]!==bkUp?.[k1]?.[fk2]){
						r = 1;
						data[k1] = obj[k1];
					}
				})
			}
			else if(obj[k1] &&  obj[k1]!==bkUp[k1]){
				r = 1;
				data[k1] = obj[k1];
			}
		}
	}
	return {data, hasChange:r};
  }
